import React, { useEffect, useState } from "react";
import "chart.js/auto";
import { Doughnut } from "react-chartjs-2";
import { Card } from "react-bootstrap";

const initData = {
  labels: [],
  datasets: [],
};

const SubscribersByMessenger = ({ period }) => {
  const [data, setData] = useState(initData);

  useEffect(() => {
    const fetchDataBasedOnPeriod = (selectedPeriod) => {
      const dataForPeriod = getDummyDataForPeriod(selectedPeriod);
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(dataForPeriod);
        }, 600);
      });
    };

    fetchDataBasedOnPeriod(period)
      .then((result) => {
        setData(result);
      })
      .catch((error) => {
        console.error("Error fetching data: ", error);
      });
  }, [period]);

  const getDummyDataForPeriod = (selectedPeriod) => {
    switch (selectedPeriod) {
      case " ":
        return {
          labels: ["", ""],
          datasets: [
            {
              data: [2, 10],
              backgroundColor: ["#007bff", "#28a745"],
            },
          ],
        };
      case " 7 ":
        return {
          labels: ["", ""],
          datasets: [
            {
              data: [7, 40],
              backgroundColor: ["#007bff", "#28a745"],
            },
          ],
        };
      case " ":
        return {
          labels: ["", ""],
          datasets: [
            {
              data: [45, 5],
              backgroundColor: ["#007bff", "#28a745"],
            },
          ],
        };
      case " ":
        return {
          labels: ["", ""],
          datasets: [
            {
              data: [40, 60],
              backgroundColor: ["#007bff", "#28a745"],
            },
          ],
        };
      case " ":
        return {
          labels: ["", ""],
          datasets: [
            {
              data: [50, 50],
              backgroundColor: ["#007bff", "#28a745"],
            },
          ],
        };
      default:
        return initData;
    }
  };

  const options = {
    legend: {
      display: true,
      position: "bottom",
    },
  };

  return (
    <Card>
      <Card.Header>{`    `}</Card.Header>
      <Card.Body>
        <div>
          <Doughnut data={data} options={options} />
        </div>
      </Card.Body>
    </Card>
  );
};

export default SubscribersByMessenger;
